import {
	mapState,
	mapMutations
} from 'vuex';

import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			empty: false, //空白页现实  true|false
			collectList:[]
		};
	},
	onLoad() {
		
	},
	onShow(){
		this.getMenuCollectList()
	},
	watch: {
		//显示空白页
		collectList(e) {
			console.log('watch')
			let empty = e.length === 0 ? true : false;
			if (this.empty !== empty) {
				this.empty = empty;
			}
		}
	},
	computed: {
		...mapState(['hasLogin'])
	},
	methods: {
		...mapMutations(['login']),
		// 请求用户收藏列表数据
		async getMenuCollectList(){
			const result =await  this.$common.$service.$menu.getMenuCollectList();
			this.collectList =  result.data
			console.log(this.collectList)
		},
		
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		
		//监听image加载失败
		onImageError(key, index) {
			console.log('onImageError',this.collectList)
			this[key][index].img = '/static/errorImage.jpg';
		},
		navToLogin() {
			uni.getUserProfile({
				lang: 'zh_CN',
				desc: '登录',
				success: (res) => {
					const {nickName,avatarUrl,gender} = res.userInfo;
					const  userInfo = {
						nickname:nickName,
						avatarUrl,
						gender
					}
					this.updataUserInfo(userInfo)
				},
				fail: (res) => {
					console.log(res)
				}
			});
		},
		updataUserInfo(userInfo) {
			this.$common.$service.$user.updataUserInfo(userInfo).then((res) => {
				if (res && res.code == 200) {
					console.log('更新用户成功')
					this.login(userInfo);
				}
			})
		},
	}
}
